.form-container {
    width: 100%;
    margin-bottom: 20px;
    display: flex;
    border-radius: calc(0.5 * 100px);
    box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.38);
    justify-content: space-evenly;

    .input-text {
        font-size: 1rem;
        font-weight: 400;
        width: 85%;
        padding-right: 5px;
        padding-left: 10px;
        border-radius: calc(0.5 * 100px);
    
        &::placeholder {
            color: #000;
        }
    }
    
    .input-submit {
        background: transparent;
        color: #5b5b5b;
        text-transform: capitalize;
        cursor: pointer;
        font-weight: 600;
        text-align: center;
        margin-right: 0;

        svg {
            width: 22px;
            height: 22px;
        }
    }
    
    .input-text,
    .input-submit {
        height: 45px;
        outline: none;
        border: none;
    }
}